@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
svg {
  background: inherit;
}
.pult svg {
  font-size: 40px;
}
.pult svg :hover {
  font-size: 40px;
  fill: lightcoral;
}
.menu a {
  background: inherit;
  font-size: 25px;
}

