.layout {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  main {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__panel {
    align-self: flex-start;
    width: 100%;
  }
}
